<template>
  <div class="grid min-h-screen place-items-center">
    <loader />
  </div>
</template>

<script>
export default {
  created() {
    let userid = this.$route.query.id;
    if (userid) {
      this.$store.dispatch("authenticate", {
        userid,
        router: this.$router,
        route: this.$route,
      });
    } else {
      this.$router.replace({ name: "restricted" });
    }
  },
};
</script>

